<template>
  <div class="main-header">
    <div class="inner">
      <div class="left-menu">
        <router-link :to="{ name: 'MainPage' }" class="logo"><h1></h1></router-link>
        <router-link :to="{ name: 'ExpertPage' }" class="go-expert"> </router-link>
      </div>
      <div class="sub-menu">
        <ul>
          <li><a id="login" @click="loginOpen = true" v-if="member===0"></a>
              <a id="mypage" v-else href="/mypage"></a>
          </li>
          <li>
              <a id="signup" @click="signupOpen = true" v-if="member===0">3 </a>
              <a id="logout" v-else @click="logout"></a>
          </li>
        </ul>
      </div>
    </div>
    <!--   -->
    <div class="black-bg" v-if="loginOpen == true">
      <div class="white-bg">
        <div class="close-box">
        <a class="close-btn" @click="loginOpen = false"></a>
        </div>
        <login />
        <p>  ?<a @click="signupOpen = true, loginOpen = false"> 3 </a></p>
      </div>  
    </div>
    <!--   -->
    <div class="black-bg" v-if="signupOpen == true">
      <div class="white-bg">
        <div class="close-box">
          <a class="close-btn" @click="signupOpen = false"></a>
        </div>
        <signup />
        <p> ?<a @click="loginOpen = true, signupOpen = false"> </a></p>
      </div>
    </div>
  </div>
</template>

<script>
import Login from './Login'
import Signup from './Signup'
import axios from 'axios'

export default {
  name: 'MainHeader',
  data() {
    return {
      loginOpen : false,
      signupOpen : false,
      member:0
    }
  },
  mounted(){
     //  ,  
    const token=localStorage.getItem('access_token');
    this.members(token)//  
  },
  components: {
    Login,
    Signup
  },
  methods:{
    //  
    removeTokens(){
      localStorage.removeItem('access_token');
      localStorage.removeItem('refresh_token');
      localStorage.removeItem('stayLogin');
    },
    members(token){
      //  ,  
      let token_ck = localStorage.getItem('access_token')
      let stayLogin = localStorage.getItem('stayLogin');
      let social = localStorage.getItem('is_social');
      let refresh_tokens = localStorage.getItem("refresh_token");
      let param = document.location.href.split("/");
       console.log(token);
       if(token===null){//  
        this.member=0;// 
        console.log(' ');
      }
      else if(token!=null&&stayLogin === "false"){//   ,   
         this.member=1;
        console.log(" ",token_ck===token);
        console.log(" ",stayLogin);
        console.log("",social);
        if(token_ck!=null){// 
        //  
        setTimeout(function () {
         this.removeTokens();
          alert('   .   appvue')
          this.member=0;
          location.href="http://localhost:8080/"
          },3600000)
        }
      }
      else if(token!=null||stayLogin === "true"){//  
        this.member=1;// 
        console.log('11111111');
        console.log(" ",token_ck===token);
        console.log(" ",stayLogin);
        console.log("",social);
        setInterval(()=>
          //30  refreshToken
          axios.post('http://118.67.135.33/accounts/token/refresh/', {
            refresh: refresh_tokens
          })
          .then(response => {
            console.log("refreshToken",response);
          })
          .catch(error => {
            console.log(' .');
          }),1800000)
          //3540000
      }
    },
    // 
    logout(){
      // let r_token=localStorage.getItem("refresh_token")
      // console.log(r_token);
      // console.log(localStorage.getItem("access_token"));
      // // 
      // localStorage.removeItem('stayLogin');
      // axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.getItem("access_token");
      // axios
      // .post('http://118.67.135.33/accounts/logout/', {
      //   refresh:r_token
      // })
      // .then(response => {
      //   console.log(response);
      //    if(response.request.status==200){
      //      //   
      //       this.deleteTokens();
      //       localStorage.removeItem('is_social');
      //       //
      //      location.href="http://localhost:8080/"
      //      alert(' !')
      //       // 
      //      this.member=0;
      //    }
      // })
      // .catch(error => {
      //   console.log(" ");
      // })

      // 
      this.removeTokens();
      //   
       const token=localStorage.getItem('access_token');
       console.log(token)
        if(token == null){
          console.log("")
          this.member=0;
          location.href="http://localhost:8080/"
          alert(' !')
        }
    }
  }
}
</script>




