<template>
  <div id="app">
    <router-view/>
    <!-- <app-footer /> -->
  </div>
</template>

<script>
// import AppFooter from '@/components/AppFooter'
import axios from 'axios';

export default {
  name: 'App',
  components: {
  },
  mounted(){
    //  ,  
    const token=localStorage.getItem('access_token');
    this.members(token)//  
  },
  methods: {
    //  
    deleteTokens(){
      localStorage.removeItem('access_token');
      localStorage.removeItem('refresh_token');
      localStorage.removeItem('stayLogin');
    },
    members(token){
      //  ,  
      let token_ck= localStorage.getItem('access_token')
      let stayLogin=localStorage.getItem('stayLogin');
      let social=localStorage.getItem('is_social');
      let refresh_tokens=localStorage.getItem("refresh_token")
      let param= document.location.href.split("/");
      if(param[3]==="editor"){
          console.log(' ');
          setInterval(()=>
          //59  refreshToken
          axios.post('http://118.67.135.33/accounts/token/refresh/', {
            refresh: refresh_tokens
          })
          .then(response => {
            console.log("refreshToken editor page",response);
          })
          .catch(error => {
            console.log(' .');
          }),3540000)
      }
      else if(token!=null&&stayLogin === "false"){//   ,   
        console.log(" ",token_ck===token);
        console.log(" ",stayLogin);
        console.log("",social);
        if(token_ck!=null){// 
        //  
        setTimeout(function () {
         this.deleteTokens();
          alert('   .   appvue')
          location.href="http://localhost:8080/"
          },3600000)
        }
      }
      else if(token!=null||stayLogin === "true"){//  
        console.log(" ",token_ck===token);
        console.log(" ",stayLogin);
        console.log("",social);
        setInterval(()=>
          //30  refreshToken
          axios.post('http://118.67.135.33/accounts/token/refresh/', {
            refresh: refresh_tokens
          })
          .then(response => {
            console.log("refreshToken",response);
          })
          .catch(error => {
            console.log(' .');
          }),1800000)
          //3540000
      }
    },
  },
  
}
</script>

<style>
/* reset.css */
@import "./styles/reset.css";
/* common.css */
@import "./styles/common.css";
/* main.css */
@import "./styles/main.css";
/* editor.css */
@import "./styles/editor.css";
/* mypage.css */
@import "./styles/mypage.css";
/* expert.css */
@import "./styles/expert.css";

#app {
  width: 100%;
  margin: 0 auto;
}
</style>



