<template>
  <div class="editor-header">
    <div class="left">
    <!--    -->
      <a href="/"><img src="../assets/images/home.png" alt=""></a>
    </div>   
    <div class="mid">
    <!-- redo / undo -->
      <div class="before-after-box">
        <a><img src="../assets/images/left-arrow.png" alt=""></a>
        <a><img src="../assets/images/right-arrow.png" alt=""></a>
      </div>
      <div class="right">
        <a id="export" class="export" @click="exportOpen = true">Export</a>
        <a id="help" class="more" @click="helpOpen"><img src="../assets/images/threedots.png" alt=""></a>
      </div>
    </div>     
    <!-- export() -->
    <div class="export">
      <div class="white-bg" v-if="exportOpen">
        <div class="close-box" @click="exportOpen = false"></div>
        <export />
      </div>
    </div>
    <!-- help() -->
    <div class="help">
        <div class="white-bg" id="help-slide">
          <div class="close-box" @click="helpClose"></div>
          <div v-if="expertOpen == false">
          <ul class="help-list help-select" v-if="qnaListOpen == false">
            <li @click="expert"> <a class="help-next btn"></a></li>
            <li @click="qnaOpen">HELP<a class="help-next btn"></a></li>
          </ul>
          </div>

        <!-- expert() -->
            <div class="expert" v-if="expertOpen == true">
              <div>
                <div>
                  <a @click="moreBack" class="back" alt=""></a>
                  <ul class="expert-contain">
                    <li>
                      <div class="expert-top">
                        <div>  <br>, , , CG<br>HIGH-Quality  </div>
                        <div class="expert-button" @click="findmedia"> </div>
                        <img src="../assets/images/findmedia.png" alt="">
                      </div>
                    </li>
                    <li>
                      <div class="expert-bottom">
                        <div>  <br>  </div>
                        <div type="button" class="expert-button" @click="expertGo">  </div>
                        <img src="../assets/images/expert_go.png" alt="  ">
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          <!--  -->
          <div v-if="qnaListOpen == true">
          <div class="help">
            <a @click="moreBack" class="back" alt=""></a>
              <p class="help-title" ref="title">{{title}}</p>
              <!--   -->
              <ul class="help-list" v-if="qlist==true">
                  <li @click="question"> , <a @click="question" class="help-next btn"></a></li>
                  <li @click="question"> , <a @click="question" class="help-next btn"></a></li>
                  <li @click="question">  <a @click="question" class="help-next btn"></a></li>
                  <li @click="question"> <a @click="question" class="help-next btn"></a></li>
                  <li @click="question"> <a @click="question" class="help-next btn"></a></li>
                  <li @click="question">  <a @click="question" class="help-next btn"></a></li>
                  <li @click="question">    <a @click="question" class="help-next btn"></a></li>
              </ul>
              <!--  -->
              <div  v-if="qlist==false">
                <a @click="listBack" class="back" alt=""></a>
                <div class="help-answer">  </div>
              </div>
            </div>
          </div>

      </div>
    </div>
    
    </div>
</template>

<script>
import Export from './Export'
import axios from 'axios'

export default {
  name: 'EditorHeader',
  data() {
    return {
      exportOpen : false,
      qnaListOpen: false,
      expertOpen : false,
      qlist:true,
      title:"   ?",
    }
  },
  components: {
    Export
  },
  mounted() {
  },
  methods:{
    //help() 
    helpOpen(){
      let elem = document.getElementById("help-slide");
      elem.style.transition = "all .5s ease";
      elem.style.height = "200px";
      this.qnaListOpen = false;
      this.expertOpen=false;
    },
    //help() 
    helpClose(){
      let elem = document.getElementById("help-slide");
      elem.style.transition = "all .5s ease-in-out";
      elem.style.height = "0px";
      elem.style.width = "300px";
      elem.style.left = "82%";
    },
    qnaOpen() {
      this.qnaListOpen = true;
      let elem = document.getElementById("help-slide");
      elem.style.transition = "all .5s ease";
      elem.style.height = "400px";
    },
    //  
    expert(){
      this.expertOpen=true;
      let elem = document.getElementById("help-slide");
      elem.style.transition = "all .5s ease";
      elem.style.height = "600px"
      elem.style.width = "1000px";
      elem.style.left = "44%";

    },
     // 
    moreBack(){
      this.expertOpen=false
      let elem = document.getElementById("help-slide");
      elem.style.transition = "all .5s ease";
      elem.style.height = "200px";
      elem.style.width = "300px";
      elem.style.left = "82%";
      this.qnaListOpen = false;
    },
    //help  faq 
    listBack(){
      this.qlist=true;
    },
    // 
    back(){
        this.qlist=true
        this.title="   ?"
        let elem = document.getElementById("help-slide");
        elem.style.transition = "all .5s ease";
        elem.style.height = "200px";
        elem.style.width = "300px";
        this.$refs.title.style="font-weight:300;"
    },
    // 
    question(e){
       let target=e.target;
       this.title=target.innerText;
       this.$refs.title.style="font-weight:600;"
       this.qlist=false
       this.expertOpen=false;
    },
    //  
    findmedia(){
      window.open('http://findmedia.co.kr/')
    },
    // 
    expertGo(){
      location.href="/expert-page"
    }
  }
}
</script>


