<template>
  <div class="export">
    <p class="export-title">{{title}}</p>
    <button type="button"  class="export-btn blue-btn" @click="encoding" v-if="loading==false">  </button>
    <!--  -->
    <div class="loading-bar" v-else>0%</div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Export',
  data() {
    return {
        title:" ?",
        loading:false
    }
  },
  mounted(){

  },
  methods: {
    // 
    encoding(){
        this.title=" ...";
        this.loading=true
      }
    }
}
</script>




