<template>
  <div class="mypage">
    <!-- Header -->
    <main-header />
    <!-- My Page -->
    <div class="inner">
      <p class="title"> </p>
      <p class="sub-title">   , !</p>
      <div class="project-container">
          <ul>
            <li v-for="(p, index) in list" :key="index">
              <img :src="p.image" class="project-img" >
              <div class="project-info">
                <p>{{ p.date }}</p>
                <a class="save-btn"></a>
                <a class="delete-btn" @click="deleteOpen = true"></a>
              </div>
            </li>
          </ul>
      </div>
      <!--   -->
      <p class="title"> </p>
      <p class="sub-title">    .</p>
      <table class="pd_table">
      <thead>
          <tr>
              <td>no</td>
              <td>
                <select @change="onChange()" id="state">
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </td>
              <td> (  )</td>
              <td></td>
              <td></td>
          </tr>
      </thead>
      {{lists}}
        <tbody v-if="part==1" class="tbody">
          
          <tr v-for="(p, index) in carrays" :key="index">
              <td>{{ea-index}}</td>
              <td>{{carrays[index].status}}</td>
              <td @click="showDetails(index)">
                <ul class="request">
                  <li>{{carrays[index].types[0]}} , {{carrays[index].types[1]}}</li>
                  <li>{{carrays[index].length}}</li>
                  <li>{{carrays[index].option[0]}}, {{carrays[index].option[1]}}</li>
                  <li>{{carrays[index].make_length[0]}}</li>
                </ul>
              </td>
              <td>{{(carrays[index].created_at).slice(0,10)}}</td>
              <td>
                <div class="del-btn" @click="delRequest(carrays[index].id)">
                  <img src="../assets/images/delete.png" alt="">
                  <input type="button" value="">
                </div>
              </td>
          </tr>
        </tbody>
          <tr v-else-if="part==2">
            <td colspan="4">  .</td>
          </tr>
      </table>
      <!--   -->
      <div class="black-bg" v-if="detailOpen == true">
        <div class="white-bg detail-white-bg">
          <div class="close-box">
            <button class="close-btn" @click="detailOpen = false"></button>
          </div>
          <div class="register-details">
            <p>{{(this.arrays[this.dIndex].created_at).slice(0,10)}} </p>
            <ul class="detail-list">
              <li>1. : {{this.arrays[this.dIndex].types[0]}} {{this.arrays[this.dIndex].types[1]}} {{this.arrays[this.dIndex].types[2]}} {{this.arrays[this.dIndex].types[3]}}</li>
              <li> &nbsp;&nbsp;   : {{this.arrays[this.dIndex].type_etc}}
              <li>2. : {{this.arrays[this.dIndex].length}}</li>
              <li> &nbsp;&nbsp;   : {{this.arrays[this.dIndex].length_etc}}
              <li>3. : {{this.arrays[this.dIndex].option[0]}} {{this.arrays[this.dIndex].option[1]}} {{this.arrays[this.dIndex].option[2]}} {{this.arrays[this.dIndex].option[3]}}</li>
              <li> &nbsp;&nbsp;   : {{this.arrays[this.dIndex].option_etc}}
              <li>4. : {{this.arrays[this.dIndex].make_length[0]}}</li>
              <li>
                5.   
                <ul>
                  <li>: {{this.arrays[this.dIndex].manager}}</li>
                  <li>: {{this.arrays[this.dIndex].phone}}</li>
                  <li>: {{this.arrays[this.dIndex].company}}</li>
                  <li>: {{this.arrays[this.dIndex].email}}</li>
                  <li>: {{this.arrays[this.dIndex].request_etc}}</li>
                </ul>
              </li>
            </ul>
            <div class="del-btn" @click="delRequest(e)">
              <img src="../assets/images/delete.png" alt="">
              <input type="button" value="">
            </div>
          </div>
        </div>
      </div>
    </div>
    <a class="change-btn" id="change-btn" @click="personalOpen = true"> </a>
    <!--   -->
    <div class="black-bg" v-if="deleteOpen == true">
      <div class="white-bg delete-white-bg">
        <div class="close-box">
          <button class="close-btn" @click="deleteOpen = false"></button>
        </div>
        <div class="delete-ok">
          <p> ?</p>
          <div class="yes-no">
            <a></a>
            <a @click="deleteOpen = false"></a>
          </div>
        </div>
      </div>
    </div>
     <!--   -->
    <div class="black-bg" v-if="personalOpen == true">
      <div class="white-bg">
        <div class="close-box">
          <button class="close-btn" @click="personalOpen = false"></button>
        </div>
        <div class="change-form" v-if="personal == false">
          <p class="title"> </p>
          <p class="notice">
            , ,   1  ,<br>
             8   .
          </p>
            <ul class="change-form">
              <li class="password1-input">
                <img src="../assets/images/password.png" alt=" ">
                <input @click="onkeydown" ref="password" v-model="password" type="password" placeholder=" " style="background-color:transparent;" autofocus>
              </li>
              
              <li class="password1-input">
                <img src="../assets/images/password.png" alt=" ">
                <input @click="onkeydown" ref="password1" v-model="password1" type="password" placeholder=" " style="background-color:transparent;">
              </li>
             <li class="password2-input">
                <img src="../assets/images/password.png" alt=" ">
                <input @click="onkeydown" ref="password2" v-model="password2" type="password" placeholder=" " style="background-color:transparent;">
              </li>
            </ul>
          <a class="blue-btn" @click="passChange"> </a>  
        </div>
        <!--   -->
        <div class="change-ok" v-if="personal == true">
          <p class="title">
            <br>
            .
          </p>
          <p>5  .</p>
        </div>
      </div>
    </div>
    <!-- Footer -->
    <app-footer />
  </div>
</template>

<script>
import MainHeader from '../components/MainHeader.vue'
import data from '../assets/projects.js'
import AppFooter from '../components/AppFooter.vue'
import axios from 'axios';
export default {
  components: { MainHeader, AppFooter },
  name: 'MyPage',
  data() {
    return {
      personalOpen:false,
      personal:false,
      deleteOpen : false,
      detailOpen : false,
      list : data,
      arrays:[],
      carrays:[],
      types:[],
      length:[],
      option:[],
      make_length:[],
      dIndex: 0,
      ea:0,
      part:1,
      e: "",
      password:"",
      password1:"",
      password2:""
    }
  },
  mounted() {
    //  console.log(localStorage.getItem("access_token"))
    let is_social=localStorage.getItem("is_social")
    if(is_social==="true"){
      document.getElementById("change-btn").style.display="none"
    }
  },
  methods:{
    //  
    onChange(){
      let select=document.getElementById("state")
      let state=select.options[document.getElementById("state").selectedIndex].value;
      //  filter
      if(state==""){
        this.carrays=this.arrays.filter(function(d,n,all){
          return d;
        });
      }
      else{
        this.carrays=this.arrays.filter(function(d,n,all){
           let z=d.status.indexOf(state);
            console.log(z);
              if(z!=-1){
                return d;
              }
        });
      }
    },
    // 
    delRequest(e){ 
      // console.log(e);
      e = this.arrays[this.dIndex].id
      axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.getItem("access_token");
      axios.delete('http://118.67.135.33/outsourcings/'+e)
        .then(function (response) {
          // console.log(response);
          alert("  !")
          location.href="/mypage";
        })
        .catch(function (error) {
          console.log(" ");
        })
    },
    //  
    showDetails (index) {
      this.detailOpen = true;
      this.dIndex = index
    },
    //   css
    onkeydown(e){
      let target=e.target
      target.parentNode.style.borderBottom="1px solid #555"
    },
    // 
    passChange(){
      if (this.password=="") {
        alert("  !");
        this.$refs.password.focus();
      }
      else if(this.password1=="" ||this.password2==""){
        alert("  !");
        this.$refs.password1.focus();
      }
      // , ,   1  , 8  16   
      else if (!/^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+]).{8,16}$/.test(this.password1)) {  
          alert(" , ,   1  , 8  16   .")
          this.$refs.password1.focus();
      }
      else if (this.password1!=this.password2 ) {
        alert('  ')
        this.$refs.password2.focus();
      }
      //   
      else{
        let token=localStorage.getItem('access_token');
        axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.getItem("access_token");
        axios.post('http://118.67.135.33/accounts/password/change/', {
          headers:{
            Authorization:`Bearer ${token}`
          },
          old_password:this.password,
          new_password1:this.password1,
          new_password2:this.password2,
        })
          .then(response => {
            if(response.request.status==200){
              alert("  !")
            }
            this.personalOpen = false
            })
          .catch(error => {
            console.log(' .');
          })
      }
    },
  },
   computed:{
    lists(){//  
    // console.log(localStorage.getItem("access_token"));
    console.log(localStorage.getItem("access_token"));
    axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.getItem("access_token");
    axios
      .get('http://118.67.135.33/outsourcings/mine', {
      })
      .then(response => {
        console.log(response);
        if(!response.data.length==0){
              this.part===1 // 
              this.arrays=response.data
              this.carrays=response.data
              this.ea=response.data.length
               for(let i=0; i<this.ea; i++){
                  let i2=this.arrays[i].types.length
                  for(let a=0;a<i2;a++){
                     // 
                     this.types_ea=a
                     switch(this.arrays[i].types[a]){
                       case "Youtube":
                       this.arrays[i].types[a]="";
                       break;
                       case "AD":
                       this.arrays[i].types[a]="";
                       break;
                       case "Promotion":
                       this.arrays[i].types[a]="";
                       break;
                       case "Graphic":
                       this.arrays[i].types[a]="";
                       break;
                       case "etc":
                       this.arrays[i].types[a]="";
                       break;
                     }
                     // 
                    switch(this.arrays[i].length){
                      case "min_1":
                      this.arrays[i].length="1 ";
                      break;
                      case "min_5":
                      this.arrays[i].length="5 ";
                      break;
                      case "min_10":
                      this.arrays[i].length="10 ";
                      break;
                      case "min_30":
                      this.arrays[i].length="30 ";
                      break;
                      case "etc":
                      this.arrays[i].length="";
                      break;
                    }
                     for(let a=0;a<=i2;a++){
                      // 
                      switch(this.arrays[i].option[a]){
                        case "All":
                        this.arrays[i].option[a]=" "
                        break;
                        case "BGM":
                        this.arrays[i].option[a]=""
                        break;
                        case "Effect":
                        this.arrays[i].option[a]=""
                        break;
                        case "Motion_Graphic":
                        this.arrays[i].option[a]=""
                        break;
                        case "Subtitle":
                        this.arrays[i].option[a]=""
                        break;
                        case "Correction":
                        this.arrays[i].option[a]=""
                        break;
                        case "Narration":
                        this.arrays[i].option[a]=""
                        break;
                        case "etc":
                        this.arrays[i].option[a]=""
                        break;
                        }
                     }
                    // 
                     switch(this.arrays[i].make_length[0]){
                       case "week_1":
                       this.arrays[i].make_length[0]="1 "
                       break;
                       case "week_2":
                       this.arrays[i].make_length[0]="2 "
                       break;
                       case "month_1":
                       this.arrays[i].make_length[0]="1 "
                       break;
                       case "After_Consulting":
                       this.arrays[i].make_length[0]=" "
                       break;
                       }
                      // 
                     switch(this.arrays[i].status){
                       case "requested":
                       this.arrays[i].status=""
                       break;
                       case "working":
                       this.arrays[i].status=""
                       break;
                       case "done":
                       this.arrays[i].status=""
                       break;
                       }
                  }
              }
          }
          else{// 
              this.part=2
          }
      })
      .catch(error => {
        console.log(error);
      })
    }
  }
}
</script>


