<template>
  <div class="upload-slide">
  <div v-if="loading==false">
    <a class="upload-btn"> </a>
    <div class="upload-box drag-area">
      <p class="upload-header">      </p>
      <input type="file" name="file" id="file" accept="video/mp4,video/mkv,video/x-m4v,video/*" hidden >
    </div>
        <p>  : mp4</p>
        <div style="position: fixed;top: 20rem;padding: 1.5rem; border: 3px solid gray;">
            <h4 style='color: #4141ff;'>Control box</h4>
            <button type="button" id='deleteBtn' class='btn btn-danger btn-sm'>  </button>
            <button type="button" id='undo' class='btn btn-warning btn-sm'>  </button>
            <br><br>
            <button type="button" id='play' class='btn btn-success btn-sm'></button>
            <br><br>
            <button type="button" id='cutting' class='btn btn-primary btn-sm'>  </button>
            <br><br>
            <button type="button" id='apply' class='btn btn-info btn-sm'> &  </button>
            <div id='downloadLinkArea'>
                <br>
                <h4>*   </h4>
            </div>
        </div>
        <div class='curtain'>  .....</div>
  </div>
  <!--   -->
    <div v-if="loading">
    <div class="loading-contain">
      <img src="../assets/images/uploading.png">
      <div class="loaging-circle">
    <div class="loaging-inner"></div>
    <div class="loaging-number">{{loadingNumber}}</div>
    <div class="circle">
      <div class="bar left">
        <div class="progress"></div>
      </div>
      <div class="bar right">
        <div class="progress"></div>
      </div>
    </div>
  </div>
      <div class="loading-title">   ...</div>
    </div>
    </div>
  </div>
  
</template>

<script>
export default {
  name: 'UploadSlide',
  data(){
    return {
      // handleSelected,
      loading:false,
      loadingNumber:"0 %"
    }
  },
  mounted(){
    // 
    const numb = document.getElementsByClassName("loading-number");
    let counter = 0;
    setInterval(() => {
      if(counter == 100 ){
        clearInterval();
      }
      else{
        counter+=1;
        this.loadingNumber = counter + "%";
      }
    }, 80);
  },
  methods:{
    handleSelected(e) {
      console.log(e);
      const selectedFile = e.target.files[0];
      if (selectedFile) {
        addListeners(reader);
        reader.readAsDataURL(selectedFile);
      }
    }
  }
}
</script>



